import { $axios } from "./app-http";

export async function getFertilizationByCycleId(cycleId) {
    const {
        data: { fertilization }
    } = await $axios.get("fertilization/by-cycle-id", { params: { cycleId } });

    return fertilization;
}

export async function createFertlization(payload) {
    const { data } = await $axios.post(
        "fertilization/create-fertilization",
        payload
    );
    return data;
}

export async function updateFertlization(payload) {
    const { data } = await $axios.post(
        "fertilization/save-fertilization",
        payload
    );
    return data;
}

export async function getFertilizationLine(fertId) {
    const {
        data: { fertilizationLines }
    } = await $axios.get("fertilization/fertilization-lines", {
        params: { fertId }
    });
    return fertilizationLines;
}

export async function createFertilizationLine(payload) {
    const {
        data: { fertLine }
    } = await $axios.post("fertilization-embryo-details/save-embryo", {
        ...payload
    });
    return fertLine;
}

export async function getOocyteEmbryoOptions() {
    const {
        data: { maturityList }
    } = await $axios.get("fertilization/maturity-list");

    const {
        data: { stageList }
    } = await $axios.get("fertilization/stage-list");

    const {
        data: { gradeList }
    } = await $axios.get("fertilization/grade-list");

    return { maturityList, stageList, gradeList };
}

export async function saveFertlizationEmbryoDetail(payload) {
    const { data } = await $axios.post(
        "fertilization-embryo-details/save-details",
        {
            ...payload
        }
    );

    return data;
}

export async function getFertlizationEmbryoDetail(oocId) {
    const {
        data: { fertilizationEmbryoDetails }
    } = await $axios.get("fertilization-embryo-details/get-details", {
        params: {
            oocId
        }
    });
    return fertilizationEmbryoDetails;
}
