import {
    createDaignosis,
    getDaignosisList,
    updataDaignosis
} from "@/service/maintanance";

const state = () => {
    return {
        diagnosisList: []
    };
};

const actions = {
    async getDaignosisList({ state }) {
        try {
            const { diagnosisInventoryList } = await getDaignosisList();
            state.diagnosisList = diagnosisInventoryList;
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async createDaignosisInventory({ dispatch }, payload) {
        try {
            const data = await createDaignosis(payload);
            dispatch("getDaignosisList");
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async updateDaignosisInventory({ dispatch }, payload) {
        try {
            const data = await updataDaignosis(payload.id, payload);
            dispatch("getDaignosisList");
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    }
};

export default {
    namespaced: true,
    state,
    actions
};
