import * as utilService from "@/service/utils";

const state = () => ({
    born: null,
    complications: null,
    treatmentOutcome: null,
    typeOfPregnancy: null,
    chorionicity: null,
    electiveFetalReduction: null,
    pregnancyBirthOutcome: null,
    priorBirths: null,
    priorFreshCyclesStarted: null,
    priorETsOwnOocytes: null,
    priorETsDonorOocytes: null,
    typeOfFunding: null,
    reasonsForTreatmentCycle: null,
    reasonsForMaleFactor: null,
    typeOfStimulationProtocol: null,
    medicationForCOS: null,
    triggerMedication: null,
    reasonCancelled: null,
    oocyteOrigin: null,
    donorProviderstatus: null,
    oocyteCryoMethod: null,
    spermOrigin: null,
    spermDonorStatus: null,
    spermSource: null,
    inseminationMethod: null,
    reasonsForNoUse: null,
    embryoStageFreezing: null,
    reasonEmbryoCryopreservation: null,
    embryoFreshCryoMethod: null,
    embryoStageThawing: null,
    reasonsNoET: null,
    preparationUterineLining: null,
    assistedZonaHatching: null,
    sourceTransferredEmbryos: null,
    transferInseminationMethod: null,
    charTransferredEmbryo: null,
    numberThaws: null,
    sitePGTembryo: null
});

const actions = {
    async getBornOptions({ state, commit }) {
        try {
            const data = await utilService.getBornOptions();
            commit("SET_BORN_OPTIONS", data);
            state.born = data;
        } catch (err) {
            console.log("born store");
        }
    }
};

const mutations = {
    SET_BORN_OPTIONS(state, data) {
        state.complications = data.complications;
        state.treatmentOutcome = data.treatmentOutcome;
        state.typeOfPregnancy = data.typeOfPregnancy;
        state.chorionicity = data.chorionicity;
        state.electiveFetalReduction = data.electiveFetalReduction;
        state.pregnancyBirthOutcome = data.pregnancyBirthOutcome;
        state.priorBirths = data.priorBirths;
        state.priorFreshCyclesStarted = data.priorFreshCyclesStarted;
        state.priorETsOwnOocytes = data.priorETsOwnOocytes;
        state.priorETsDonorOocytes = data.priorETsDonorOocytes;
        state.typeOfFunding = data.typeOfFunding;
        state.reasonsForTreatmentCycle = data.reasonsForTreatmentCycle;
        state.reasonsForMaleFactor = data.reasonsForMaleFactor;
        state.typeOfStimulationProtocol = data.typeOfStimulationProtocol;
        state.medicationForCOS = data.medicationForCOS;
        state.triggerMedication = data.triggerMedication;
        state.reasonCancelled = data.reasonCancelled;
        state.oocyteOrigin = data.oocyteOrigin;
        state.donorProviderstatus = data.donorProviderstatus;
        state.oocyteCryoMethod = data.oocyteCryoMethod;
        state.spermOrigin = data.spermOrigin;
        state.spermDonorStatus = data.spermDonorStatus;
        state.spermSource = data.spermSource;
        state.inseminationMethod = data.inseminationMethod;
        state.reasonsForNoUse = data.reasonsForNoUse;
        state.embryoStageFreezing = data.embryoStageFreezing;
        state.reasonEmbryoCryopreservation = data.reasonEmbryoCryopreservation;
        state.embryoFreshCryoMethod = data.embryoFreshCryoMethod;
        state.embryoStageThawing = data.embryoStageThawing;
        state.reasonsNoET = data.reasonsNoET;
        state.preparationUterineLining = data.preparationUterineLining;
        state.assistedZonaHatching = data.assistedZonaHatching;
        state.sourceTransferredEmbryos = data.sourceTransferredEmbryos;
        state.transferInseminationMethod = data.transferInseminationMethod;
        state.charTransferredEmbryo = data.charTransferredEmbryo;
        state.numberThaws = data.numberThaws;
        state.sitePGTembryo = data.sitePGTembryo;
    }
};

export default {
    namespaced: true,
    state,
    actions,
    mutations
};
