<template>
    <p-block-ui :blocked="loading" :fullScreen="true">
        <p-progress-spinner v-show="loading" />
    </p-block-ui>

    <p-toast />
    <p-confirm-dialog />

    <p-toolbar class="p-text-bold">
        <template #left>
            <header-info />
        </template>

        <template #right>
            <p-button
                icon="pi pi-user"
                class="p-mr-2"
                @click="showUserOverlay"
            />
        </template>
    </p-toolbar>

    <p-tab-menu :model="menu" />

    <div id="main" class="p-input-filled">
        <router-view />
    </div>

    <p-overlay-panel ref="userOverlay" style="width: 300px">
        <div class="p-grid">
            <div class="p-col-fixed" style="width:50px">
                <p-avatar icon="pi pi-user" />
            </div>
            <div class="p-col">
                <strong>{{ user.name }}</strong>
                <br />
                <strong>{{ user.username }}</strong> <br />
                <strong>{{ user.email }}</strong> <br />
            </div>
        </div>
        <p-divider />
        <div class="p-d-flex p-jc-between">
            <p-button label="Change Password" @click="changePassword" />
            <p-button
                label="Log Out"
                class="p-button-warning"
                icon="pi pi-sign-out"
                @click="logout()"
            />
        </div>
    </p-overlay-panel>
</template>

<script>
import { computed, onMounted, ref } from "vue";
import { useStore } from "vuex";
import { emitter, EVENT_STRINGS } from "@/event-bus";

import router from "@/router";

import HeaderInfo from "@/components/shared/header-info.vue";

export default {
    components: {
        HeaderInfo
    },
    setup() {
        const store = useStore();
        const loading = ref(false);
        const patient = computed(() => store.state.artCycle.patient);
        const isFemalePatient = computed(
            () => store.state.artCycle.patient.sex === "F"
        );

        const menu = ref([
            { to: "/art-cycle", label: "ART Cycle" },
            {
                to: "/cycle-monitoring",
                label: "Cycle Monitoring",
                visible: isFemalePatient
            },
            { to: "/opu", label: "OPU", visible: isFemalePatient },
            { to: "/semen", label: "Semen" },
            { to: "/semen-cryo-storage", label: "Semen Cryo Storage" },
            { to: "/pregnancy", label: "Pregnancy", visible: isFemalePatient },
            {
                to: "/fertilization",
                label: "Fertilization",
                visible: isFemalePatient
            },
            {
                to: "/embryo-transfer",
                label: "Embryo Transfer",
                visible: isFemalePatient
            },
            {
                to: "/cryo-storage",
                label: "Cryo Storage",
                visible: isFemalePatient
            },
            { to: "/blood-tests", label: "Blood Tests" },
            { to: "/daily-report", label: "Daily Report" },
            { to: "/cycle-report", label: "Cycle Report" },
            { to: "/patient-message-log", label: "Patient Msg Log" },
            { to: "/message-inbox", label: "Message Inbox" },
            { to: "/born", label: "Born", visible: false },
            { to: "/test", label: "Test", visible: false },
            { to: "/maintanance", label: "Maintenance" }
        ]);

        const userOverlay = ref();
        const showUserOverlay = (event) => {
            userOverlay.value.toggle(event);
        };

        const user = computed(() => store.state.user);

        const logout = () => {
            store.dispatch("logout").then(() => {
                router.push({ name: "Login" });
            });
        };

        emitter.on(EVENT_STRINGS.LOGOUT, (data) => {
            console.log("logout from defalut");
        });

        emitter.on("app:show-page-loading", (val) => {
            loading.value = val;
        });

        onMounted(() => {
            store.dispatch("utils/getBornOptions");
        });

        return {
            menu,
            loading,
            userOverlay,
            showUserOverlay,
            logout,
            user,
            changePassword: () => router.push({ path: "/change-password" })
        };
    }
};
</script>

