import { $axios } from "@/service/app-http";

const GET_BORN_ENPOINT = "born/get-born-data-separeted";
const UPDATE_COMPLICATION_ENDPOINT = "born/update-complications";
const UPDATE_OOCYTE_COLLECTION_ENDPOINT = "born/update-oocyte-collections";
const UPDATE_OBSTETRICAL_HISTORY_ENDPOINT = "born/update-obstetrical-history";
const UPDATE_EMBRYOLOGY = "born/update-embryology";

export async function getBorn({ patientId, cycleId }) {
    const { data } = await $axios.get(GET_BORN_ENPOINT, {
        params: { patientId, cycleId }
    });
    return data;
}

export async function updateComplication(payload) {
    const { data } = await $axios.post(UPDATE_COMPLICATION_ENDPOINT, payload);
    return data;
}

export async function updatePregnancyOutcome(payload) {
    const { data } = await $axios.post(
        "born/update-pregnancy-outcome",
        payload
    );
    return data;
}

export async function updateOocyteCollection(payload) {
    const { data } = await $axios.post(
        UPDATE_OOCYTE_COLLECTION_ENDPOINT,
        payload
    );
    return data;
}

export async function updateObstetricalHistory(payload) {
    const { data } = await $axios.post(
        UPDATE_OBSTETRICAL_HISTORY_ENDPOINT,
        payload
    );
    return data;
}

export async function updateEmbryology(payload) {
    const { data } = await $axios.post(UPDATE_EMBRYOLOGY, payload);
    return data;
}

export async function updateFreshEmbryo(payload) {
    const { data } = await $axios.post("born/update-fresh-embryos", payload);
    return data;
}

export async function updateFrozenEmbryo(payload) {
    const { data } = await $axios.post("born/update-frozen-embryos", payload);
    return data;
}

export async function updateEmbryoTransfer(payload) {
    const { data } = await $axios.post("born/update-embryo-transfer", payload);
    return data;
}

export async function updateObstetricalHistoryV2(payload) {
    const { data } = await $axios.post(
        "born/update-obstetrical-history",
        payload
    );
    return data;
}

export async function updateOvarian(payload) {
    const { data } = await $axios.post("born/update-ovarian", payload);
    return data;
}
