import { createRouter, createWebHashHistory } from "vue-router";
import DefaultLayout from "@/components/layouts/defalut.vue";

const routes = [
    {
        path: "/",
        component: DefaultLayout,
        children: [
            {
                path: "",
                redirect: "/login"
            },
            {
                path: "art-cycle",
                name: "ArtCycle",
                component: () => import("@/views/art-cycle.vue")
            },
            {
                path: "fertilization",
                name: "Fertilization",
                component: () => import("@/views/fertilization.vue")
            },
            {
                path: "fertilization/create",
                name: "FertilizationCreate",
                component: () =>
                    import("@/components/fertilization/create-form.vue")
            },
            {
                path: "cycle-monitoring",
                name: "CycleMonitoring",
                component: () => import("@/views/cyccle-monitoring.vue")
            },
            {
                path: "opu",
                name: "OPU",
                component: () => import("@/views/opu.vue")
            },
            {
                path: "semen",
                name: "Semen",
                component: () => import("@/views/semen.vue")
            },
            {
                path: "semen-cryo-storage",
                name: "SemenCryoStorage",
                component: () => import("@/views/semen-cryo-storage.vue")
            },
            {
                path: "pregnancy",
                name: "Pregnancy",
                component: () => import("@/views/pregnancy.vue")
            },
            {
                path: "embryo-transfer",
                name: "EmbryoTransfer",
                component: () => import("@/views/embryo-transfer.vue")
            },
            {
                path: "cryo-storage",
                name: "CryoStorage",
                component: () => import("@/views/cryo-storage.vue")
            },
            {
                path: "blood-tests",
                name: "BloddTests",
                component: () => import("@/views/blood-tests.vue")
            },
            {
                path: "daily-report",
                name: "DailyReport",
                component: () => import("@/views/daily-report.vue")
            },
            {
                path: "cycle-report",
                name: "CycleReport",
                component: () => import("@/views/cycle-report.vue")
            },
            {
                path: "patient-message-log",
                name: "PatientMessageLog",
                component: () => import("@/views/patient-message-log.vue")
            },
            {
                path: "message-inbox",
                name: "MessageInbox",
                component: () => import("@/views/message-inbox.vue")
            },
            {
                path: "born",
                name: "Born",
                component: () => import("@/views/born.vue")
            },
            {
                path: "test",
                name: "test",
                component: () => import("@/views/test.vue")
            },
            {
                path: "redirect",
                name: "redirect",
                component: () => import("@/views/redirect.vue")
            }
        ]
    },
    {
        path: "/maintanance",
        component: DefaultLayout,
        children: [
            {
                path: "",
                component: () => import("@/views/maintanance/index.vue")
            },
            {
                path: "drug-inventory",
                component: () => import("@/views/maintanance/inventory.vue")
            },
            {
                path: "diagnosis-inventory",
                component: () => import("@/views/maintanance/daignosis.vue")
            }
        ]
    },
    {
        path: "/maintanance/users",
        component: DefaultLayout,
        children: [
            {
                path: "",
                component: () => import("@/views/users/index.vue")
            },
            {
                path: "create",
                component: () => import("@/views/users/create.vue")
            },

            {
                path: ":id/edit",
                component: () => import("@/views/users/edit.vue")
            }
        ]
    },
    {
        path: "/change-password",
        component: () => import("@/views/users/change-password.vue")
    },
    {
        path: "/login",
        component: () => import("@/views/login.vue"),
        name: "Login"
    },
    {
        path: "/:catchAll(.*)",
        component: () => import("@/views/not-found.vue")
    }
];

const router = createRouter({
    history: createWebHashHistory(process.env.BASE_URL),
    routes
});

router.beforeEach((to, from, next) => {
    const isAuthenticated = localStorage.getItem("user");

    if (to.name !== "Login" && !isAuthenticated) {
        if (to.name === "redirect") {
            next({
                name: "Login",
                query: { patient: to.query.patient, to: to.query.to }
            });
        }
        next({ name: "Login" });
    } else {
        next();
    }
});

export default router;
