import * as bornService from "@/service/born";

const state = () => ({
    complications: null,
    obstetricalHistory: null,
    ovarian: null,
    oocyteCollections: null,
    pregnancyOutcome: null,
    freshEmbryos: null,
    frozenEmbryos: null,
    embryoTransfer: null
});

const actions = {
    async getBorn({ commit }, { patientId, cycleId }) {
        try {
            const data = await bornService.getBorn({ patientId, cycleId });
            commit("SET_BORN", data);
            return Promise.resolve();
        } catch (err) {
            commit("SET_EMPTY_BORN");
        }
    },
    async getBronV2(
        { state, commit },
        { patientId, cycleId, isBornCycle, bornCycleId }
    ) {
        if (!isBornCycle) {
            return;
        }
        try {
            const {
                complications,
                obstetricalHistory,
                oocyteCollections,
                pregnancyOutcome,
                freshEmbryos,
                frozenEmbryos,
                embryoTransfer,
                ovarian
            } = await bornService.getBorn({ patientId, cycleId });

            state.complications = complications;
            state.obstetricalHistory = obstetricalHistory;
            state.oocyteCollections = oocyteCollections;
            state.pregnancyOutcome = pregnancyOutcome;
            state.freshEmbryos = freshEmbryos;
            state.frozenEmbryos = frozenEmbryos;
            state.embryoTransfer = embryoTransfer;
            state.ovarian = ovarian;

            return Promise.resolve();
        } catch (err) {
            commit("SET_EMPTY_BORN");
        }
    },
    async updateComplication({ commit }, payload) {
        try {
            const data = await bornService.updateComplication(payload);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async updatePregnancyOutcome({ state }, payload) {
        try {
            const data = await bornService.updatePregnancyOutcome(payload);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async updateOocyteCollection({ commit }, payload) {
        try {
            const data = await bornService.updateOocyteCollection(payload);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async updateObstetricalHistory({ commit }, payload) {
        try {
            const data = await bornService.updateObstetricalHistory(payload);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    },
    async updateEmbryology({ commit }, payload) {
        try {
            const data = await bornService.updateEmbryology(payload);
            return data;
        } catch (err) {
            return Promise.reject(data);
        }
    },
    async updateFreshEmbryo({ commit }, payload) {
        try {
            const data = await bornService.updateFreshEmbryo(payload);
            return data;
        } catch (err) {
            Promise.reject(err);
        }
    },
    async updateFrozenEmbryo({ commit }, payload) {
        try {
            const data = await bornService.updateFrozenEmbryo(payload);
            return data;
        } catch (err) {
            Promise.reject(err);
        }
    },
    async updateEmbryoTransfer({ commit }, payload) {
        try {
            const data = await bornService.updateEmbryoTransfer(payload);
            return data;
        } catch (err) {
            Promise.reject(err);
        }
    },
    async updateObstetricalHistory({ commit }, payload) {
        try {
            const data = await bornService.updateObstetricalHistoryV2(payload);
            return data;
        } catch (err) {
            Promise.reject(err);
        }
    },
    async updateOvarian({ commit }, payload) {
        try {
            const data = await bornService.updateOvarian(payload);
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    }
};

const mutations = {
    SET_BORN(
        state,
        { complications, obstetricalHistory, oocyteCollections, embryology }
    ) {
        state.complications = complications;
        state.obstetricalHistory = obstetricalHistory;
        state.oocyteCollections = oocyteCollections;
        state.embryology = embryology;
    },
    SET_EMPTY_BORN(state) {
        state.complications = null;
        state.obstetricalHistory = null;
        state.oocyteCollections = null;
        state.pregnancyOutcome = null;
        state.freshEmbryos = null;
        state.frozenEmbryos = null;
        state.embryoTransfer = null;
    }
};

const getters = {};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};
