import * as dailyReportService from "@/service/daily-report";

const state = () => ({
    form: { fromDate: null, toDate: null },
    reportList: [],
    selectedReport: null,
    billingList: []
});

const actions = {
    async getTotalPatient(context, fromDate, toDate) {
        try {
            const data = await dailyReportService.getTotalPatient(
                fromDate,
                toDate
            );
            return data;
        } catch (err) {
            Promise.reject();
        }
    },
    async getTotalOrders(context, fromDate, toDate) {
        try {
            const data = await dailyReportService.getTotalOrders(
                fromDate,
                toDate
            );
            return data;
        } catch (err) {
            Promise.reject();
        }
    },
    async getCycleData(context, autoTestOrderId) {
        try {
            const { data } = await dailyReportService.getCycleData(
                autoTestOrderId
            );
            return data;
        } catch (err) {
            Promise.reject();
        }
    },
    async getOrderList({ state }, { fromDate, toDate, providerNo }) {
        try {
            const data = await dailyReportService.getOrderList(
                fromDate,
                toDate,
                providerNo
            );
            state.reportList = data;
            return Promise.resolve();
        } catch (error) {
            return Promise.reject();
        }
    },
    async getPendingOrderList(context, fromDate, toDate, providerNo) {
        const data = await dailyReportService.getOrderList(
            fromDate,
            toDate,
            providerNo
        );
        return data;
    },
    async assignCycleToBloodTest({ state }, id) {
        try {
            const data = await dailyReportService.assignCycleToBloodTest({
                cycleId: id,
                testId: state.selectedReport.autoTestOrderId
            });
            return data;
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getPersonalCycles({ state }) {
        try {
            const {
                personalCycles
            } = await dailyReportService.getPersonalCycle(
                state.selectedReport.autoTestOrderId
            );
            return personalCycles;
        } catch (error) {
            return Promise.reject();
        }
    },
    async saveCheckedSigleRecord({ state }, payload) {
        try {
            const data = await dailyReportService.saveCheckedSigleRecord(
                payload
            );
            return data;
        } catch (err) {
            Promise.reject(err);
        }
    },
    async getFundedBloodBillingList({ state }, params) {
        try {
            const data = await dailyReportService.getFundedBloodBillingList(
                params
            );
            state.billingList = data;
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async getBloodBillingList({ state }, params) {
        try {
            const data = await dailyReportService.getBloodBillingList(params);
            state.billingList = data;
            return Promise.resolve();
        } catch (err) {
            return Promise.reject(err);
        }
    },
    async pushToOscar({ state }, payload) {
        try {
            const data = await dailyReportService.pushToOscar(payload);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    },
    async pushAllToOscar({ state }, payload) {
        try {
            const data = await dailyReportService.pushAllToOscar(payload);
            return data;
        } catch (err) {
            return Promise.reject();
        }
    }
};

const mutations = {
    SET_SELECTED_REPORT(state, payload) {
        state.selectedReport = payload;
    }
};

const getters = {
    isAnyReportSelected(state) {
        return state.selectedReport != null ? true : false;
    }
};

export default {
    namespaced: true,
    actions,
    state,
    mutations,
    getters
};
